import React, { Component } from "react";
import axios from "axios";
import arrowImage from "../../assets/arrow.png";
import "./mapping.css";
import "@grapecity/wijmo.styles/wijmo.css";
import * as wjNav from "@grapecity/wijmo.react.nav";
import * as wjCore from "@grapecity/wijmo";
class Mappings extends Component {
  constructor(props) {
    super(props);
    this.myRef = React.createRef();
    this.state = {
      //   you can write any property in this array objects , like key,
      // tree parent node will use header property
      // tree child node will use items property

      // you can also change header and items variable name , but then you have change
      // this variabale in tree attributes value.
      total_item: null,
      hasMore: true,
      wdata_category: [],
      wdata_product: [],
      wdata_key: null,
      wdata_category_length: null,
      wdata_product_length: null,
      wdata_length: null,
      isAnimated: true,
      autoCollapse: true,
      expandOnClick: true,
      check_bool: false,
      check_bool2: false,
      check: null,
      page: 1,
      button_disable: true,
      theposition: null,
      treeData: [
        // {
        //     header: 'Ware House 1', key:'id_21', items: [
        //         { header: 'WH Category 1', key:'id_22', items: [
        //             { header: 'Item 1.1', key:'id_23' },
        //             { header: 'Item 1.2', key:'id_24' },
        //             { header: 'Item 1.3', key:'id_25' }
        //         ] },
        //         { header: 'WH Category 2', key:'id_26', items: [
        //             { header: 'Item 1.1', key:'id_27' },
        //             { header: 'Item 1.2', key:'id_28' },
        //             { header: 'Item 1.3', key:'id_29' }
        //         ] },
        //         { header: 'Item 1.1', key:'id_30' }
        //     ]
        // }
      ],
      RefreshTreeData: [
        // {
        //     header: 'Ware House 1', key:'id_1', items: [
        //         { header: 'WH Category 1', key:'id_2', items: [
        //             { header: 'Item 1.1', key:'id_3' },
        //             { header: 'Item 1.2', key:'id_4' },
        //             { header: 'Item 1.3', key:'id_5' }
        //         ] },
        //         { header: 'WH Category 2', key:'id_6', items: [
        //             { header: 'Item 1.1', key:'id_7' },
        //             { header: 'Item 1.2', key:'id_8' },
        //             { header: 'Item 1.3', key:'id_9' }
        //         ] },
        //         { header: 'Item 1.3', key:'id_10' }
        //     ]
        // }
      ],
      treeData2: [
        // {
        //     header2: 'Shop',key:'id_11', items2: [
        //         { header2: 'S Category',key:'id_12', items2: [
        //             { header2: 'Item 1.1',key:'id_13' },
        //             { header2: 'Item 1.2',key:'id_14' },
        //             { header2: 'Item 1.3',key:'id_15' }
        //         ] },
        //         { header2: 'S Category',key:'id_16', items2: [
        //             { header2: 'Item 1.1' ,key:'id_17'},
        //             { header2: 'Item 1.2',key:'id_18' },
        //             { header2: 'Item 1.3',key:'id_19' }
        //         ] },
        //         { header2: 'Item 1.3',key:'id_20' }
        //     ]
        // }
      ],
      allowDraggingParentNodes: true,
      allowDroppingIntoEmpty: true,
    };
  }
  page = 1;
  OrignalWareHouseTreeData;
  response_nodes = [];
  server_url = "https://warehouse-22.herokuapp.com";
  UpdatedShopData;
  componentDidMount() {
    localStorage.setItem("check_render", false);
    localStorage.setItem("button_disable", true);
    this.loadInitialShopData();
    this.loadInitialWarehouseData();
    this.setState({});

    // on runnuing this component
    // here on getting data from db
    // save ware house info in State variable(TreeData and RefreshTreeData)
    // And save shop info in State variable treeData2
    // this.OrignalWareHouseTreeData=this.getRefreshTreeData();
  }

  saveMappings() {
    // updated shop data
    console.log(this.UpdatedShopData);
    // orignal ware house data
    console.log(this.state.treeData);
    console.log(this.state.RefreshTreeData);
    this.loadInitialShopData();
    this.loadInitialWarehouseData();
    let selected_nodes = this.response_nodes;
    axios
      .post(this.server_url + "/api/trees/shops", { selected_nodes })
      .then((res) => {
        console.log(res);
        console.log(res.data);
      });

    this.response_nodes = [];
  }
  refreshWareHouse() {
    // this.OrignalWareHouseTreeData=this.state.RefreshTreeData;
    if (localStorage.getItem("RefreshTreeData")) {
      this.setState({
        treeData: JSON.parse(localStorage.getItem("RefreshTreeData")),
      });
    }

    //     // ,()=>{
    //     // this.setState({RefreshTreeData:this.state.treeData})
    // console.log(this.OrignalWareHouseTreeData)
    // console.log(this.state.RefreshTreeData)
    // console.log(this.state.treeData)
  }
  render() {
    return (
      <div
        className="container-fluid"
        style={{
          paddingLeft: "8%",
          paddingRight: "8%",
          paddingTop: "60px",
          paddingBottom: "60px",
        }}
      >
        <div className="row" style={{ height: "600px" }}>
          <div
            className="col-lg-5 col-md-5 col-sm-5"
            style={{
              padding: "5%",
              boxShadow:
                "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
            }}
          >
            <h3>Ware Houses and products</h3>
            <button
              type="button"
              className="btn btn-danger float-right btn-block"
              disabled={this.state.check_bool2}
              onClick={this.fetchWarehouse}
            >
              Click to load more data
            </button>

            {/* <InfiniteScroll
              dataLength={this.state.wdata_length}
              next={this.fetchWarehouse}
              hasMore={true}
              scrollableTarget="scrollableDiv"
              loader={<h4>Loading....</h4>}
              endMessage={
                <p style={{ textAlign: "center" }}>
                  <b>Yay! You have seen it all</b>
                </p>
              }
            > */}
            <wjNav.TreeView
              ref={this.myRef}
              id="scrollableDiv"
              className="custom-tree"
              itemsSource={this.state.treeData}
              displayMemberPath="header"
              childItemsPath="items"
              allowDragging={true}
              dragStart={this.on_Drag_Start_From_WareHouse_Data.bind(this)}
              dragOver={this.on_Drag_Over_Between_WareHouse.bind(this)}
              initialized={this.initialized2.bind(this)}
              autoCollapse={false}
              expandOnClick={true}
              lazyLoadFunction={this.warehouseLazyLoad.bind(this)}
              isCollapsedChanging={this.onIsCollapsedChanging.bind(this)}
            ></wjNav.TreeView>
            {/* </InfiniteScroll> */}
          </div>

          <div
            className="col-lg-2 col-md-2 col-sm-2"
            style={{ textAlign: "center" }}
          >
            <button
              type="button"
              className="btn btn-success"
              onClick={this.saveMappings.bind(this)}
            >
              Save Mappings
            </button>
            <h2>{this.state.wdata_length}</h2>
            <img
              src={arrowImage}
              alt="Mapping"
              height={"40%"}
              width={"80%"}
              style={{ paddingTop: "80%" }}
            />
          </div>
          <div
            className="col-lg-5 col-md-5 col-sm-5"
            style={{
              padding: "5%",
              boxShadow:
                "0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19)",
            }}
          >
            <h3>Shops</h3>
            <button
              id="btnRemove"
              className="btn btn-warning"
              onClick={this.onRemoveClick.bind(this)}
            >
              Remove Selected Node
            </button>
            <wjNav.TreeView
              className="custom-tree"
              itemsSource={this.state.treeData2}
              displayMemberPath="header"
              childItemsPath="items"
              allowDragging={true}
              dragEnd={this.on_Drag_End_Toward_Shop_Data.bind(this)}
              dragOver={this.on_Drag_Over_Between_WareHouse_And_Shop.bind(this)}
              selectedItemChanged={this.onSelectedItemChanged.bind(this)}
              initialized={this.initialized.bind(this)}
              lazyLoadFunction={this.shopLazyLoad.bind(this)}
            ></wjNav.TreeView>
          </div>
        </div>
      </div>
    );
  }

  initTreeView(ctl) {
    this._wjTreeViewControl = ctl;
  }
  initialized(control) {
    this._control = control;
    this._control.selectedItem = this._control.itemsSource[0];
  }
  initialized2(control) {
    this._control2 = control;
    this._control2.selectedItem = this._control2.itemsSource[0];
  }
  onSelectedItemChanged() {
    var btn = document.getElementById("btnRemove");
    wjCore.setAttribute(
      btn,
      "disabled",
      this._control.selectedItem ? null : "disabled"
    );
  }
  onRemoveClick() {
    var control = this._control;
    if (control.selectedItem) {
      // find the array that contains the item to be removed
      var parent = control.selectedNode.parentNode;
      var arr = parent
        ? parent.dataItem[control.childItemsPath]
        : control.itemsSource;
      // remove the item from the parent collection
      var index = arr.indexOf(control.selectedItem);
      arr.splice(index, 1);
      // refresh the tree
      control.loadTree();
    }
  }
  onIsCollapsedChanging = (s, e) => {
    let node = e.node,
      tree = node.treeView;
    if (!node.isCollapsed) {
      this.setState({ check_bool2: false });
      // remove previous lazy-loaded items from data source
      node.dataItem.items = [];
      //
      // re-bind the tree to remove the old nodes
      tree.loadTree();
      localStorage.setItem("button_disable", true);
    }
    if (node.isCollapsed) {
      localStorage.setItem("button_disable", false);
    }
  };

  getRefreshTreeData() {
    return this.state.RefreshTreeData;
  }
  onCustomCSSClick(e) {
    wjCore.toggleClass(
      this._wjTreeViewControl.hostElement,
      "custom-tree",
      e.target.checked
    );
    this.setState({
      customCSS: true,
    });
  }
  on_Drag_End_Toward_Shop_Data(s, e) {
    console.log(s);
    this.setState({
      treeData: JSON.parse(localStorage.getItem("RefreshTreeData")),
    });
    this.UpdatedShopData = s.itemsSource;
    this.setState({ treeData2: this.UpdatedShopData });
    // console.log(this.getRefreshTreeData())
    // this.OrignalWareHouseTreeData=[]
    // this.OrignalWareHouseTreeData.push(this.getRefreshTreeData())
    // console.log(this.OrignalWareHouseTreeData)
    // this.setState({treeData:this.OrignalWareHouseTreeData[0]},function(){
    //     console.log(this.state.treeData);
    // });
    let response_node = JSON.parse(JSON.stringify(s.selectedNode.dataItem));
    response_node.parent = null;
    if (s.selectedNode.parentNode) {
      response_node.parent = s.selectedNode.parentNode.dataItem;
    }
    this.response_nodes.push(response_node);
    console.log(this.response_nodes);
  }
  on_Drag_Start_From_WareHouse_Data(s, e) {
    console.log("drad", s.itemsSource);
    localStorage.setItem("RefreshTreeData", JSON.stringify(s.itemsSource));
    // console.log(e.node.parentNode.itemsSource)
    // console.log(e.node.parentNode.dataItem)
    // console.log(e.node.parentNode.element)
    if (e && e.node && e.node.hasChildren) {
      e.node.isCollapsed = true; // collapse parent nodes when dragging
    }
  }

  on_Drag_Over_Between_WareHouse_And_Shop(s, e) {
    var t1 = e.dragSource.treeView;
    var t2 = e.dropTarget.treeView;
    //
    // prevent dragging within trees
    if (t1 === t2) {
      e.cancel = true;
    }
    //
    // allow dragging between trees
    if (t1 !== t2) {
      e.cancel = false;
    }
  }
  on_Drag_Over_Between_WareHouse(s, e) {
    var t1 = e.dragSource.treeView;
    var t2 = e.dropTarget.treeView;
    //
    // prevent dragging within trees
    if (t1 === t2) {
      e.cancel = true;
    }
    //
    // allow dragging between trees
    if (t1 !== t2) {
      e.cancel = true;
    }
  }
  setAllowDrag1() {
    this.setState({ allowDrag1: !this.state.allowDrag1 });
  }
  setAllowDrag2() {
    this.setState({ allowDrag2: !this.state.allowDrag2 });
  }

  loadInitialShopData() {
    var base_url = this.server_url + "/api/trees/shops";
    axios.get(base_url + "?page=1&limit=30").then((res) => {
      let data = res.data.data.results;
      let tree_data = data.map((elem) => {
        let json = {};
        if (elem.type === "category") {
          json.header = elem.name;
          json.key = elem._id;
          json.type = elem.type;
          json.items = [];
        } else if (elem.type === "productCategory") {
          json.header = elem.warehouse_product.title;
          json.key = elem.warehouse_product._id;
          json.type = elem.warehouse_product.type;
        }
        return json;
      });
      this.setState({ treeData2: tree_data });
    });
  }

  loadInitialWarehouseData() {
    var base_url = this.server_url + "/api/trees/warehouses";
    axios.get(base_url + "?page=1&limit=30").then((res) => {
      let data = res.data.data.results;
      let tree_data = data.map((elem) => {
        let json = {};
        if (elem.type === "warehouse") {
          json.header = elem.name;
          json.key = elem._id;
          json.type = elem.type;
          json.items = [];
        }
        //  else if (elem.type === 'productCategory') {
        //     json.header2 = elem.warehouse_product.title;
        //     json.key = elem.warehouse_product._id;
        //     json.type = elem.warehouse_product.type;
        // }
        console.log(json);
        return json;
      });
      this.setState({ treeData: tree_data, RefreshTreeData: tree_data });
    });
  }
  shopLazyLoad(node, callback) {
    var base_url = this.server_url + "/api/trees/shops";

    var url = base_url + "?page=1&limit=30&parent=" + String(node.dataItem.key);
    axios.get(url).then((res) => {
      let data = res.data.data.results;
      let tree_data = data.map((elem) => {
        let json = {};
        if (elem.type === "category") {
          json.header = elem.name;
          json.key = elem._id;
          json.type = elem.type;
          json.items = [];
        } else if (elem.type === "productCategory") {
          json.header = elem.warehouse_product.title;
          json.key = elem.warehouse_product._id;
          json.type = elem.warehouse_product.type;
        }
        return json;
      });
      callback(tree_data);
    });
  }
  warehouseLazyLoad(node, callback) {
    var self = this;
    setTimeout(function () {
      var result = self.getLazyData(node);
      result.then(function (result) {
        console.log("result is", result);
        callback(result);
      });

      //callback(result);
    }, 10);
  }
  getLazyData = (node) => {
    console.log("node is", node.dataItem);
    var base_url = this.server_url + "/api/trees/warehouses";
    var url = "";
    localStorage.setItem("wdata_key", node.dataItem.key);
    localStorage.setItem("wdata_name", node.dataItem.warehouse);
    localStorage.setItem("node_level", node.level);
    localStorage.setItem("node_itemsSource", node.itemsSource);
    console.log("lazy load key", localStorage.getItem("wdata_key"));
    const { page } = this.state;
    console.log("lazy in page ", this.state.page);
    if (node.level === 0) {
      console.log("in lazy load", node.dataItem.key);
      if (this.state.check_bool) {
        url =
          base_url +
          "?page=" +
          1 +
          "&limit=1&warehouse=" +
          String(node.dataItem.key);
        console.log("if id is(level0)", node.dataItem.key);
      } else {
        url =
          base_url +
          "?page=" +
          1 +
          "&limit=1&warehouse=" +
          String(node.dataItem.key);
        console.log("else id is(level0)", node.dataItem.key);
      }
    } else {
      if (this.state.check_bool) {
        url =
          base_url +
          "?page=" +
          1 +
          "&limit=10&parent=" +
          String(node.dataItem.key) +
          "&warehouse=" +
          String(node.dataItem.warehouse);
        console.log("id is(level1) if", node.dataItem.key);
      } else {
        url =
          base_url +
          "?page=" +
          1 +
          "&limit=10&parent=" +
          String(node.dataItem.key) +
          "&warehouse=" +
          String(node.dataItem.warehouse);
        console.log("id is(level1) else", node.dataItem.key);
      }
    }
    // this.setState({ loading: true });
    //this.setState({ hasError: false });

    let rtt = axios.get(url).then((res) => {
      this.setState({ hasMore: true });
      let data = res.data.data.results;
      //console.log("resp data is:", res.data.data);
      this.setState({ total_item: res.data.data.total_results });

      //   const wdata = [...this.state.wdata];
      //   wdata.push(data);
      //   console.log("warehouse data", data);
      //   this.setState({ wdata: this.state.wdata.concat(data) });
      //   console.log("array", this.state.wdata);
      let tree_data = data.map((elem) => {
        let json = {};
        if (elem.type === "category") {
          json.warehouse = elem.warehouse;
        }
        if (elem.type === "category" || elem.type === "warehouse") {
          //console.log("i am first");
          json.header = elem.name;
          json.key = elem._id;
          json.type = elem.type;
          json.items = [];
        } else if (elem.type === "productCategory") {
          // console.log("i am second");
          json.header = elem.warehouse_product.title;
          json.key = elem.warehouse_product._id;
          json.type = elem.warehouse_product.type;
        }
        return json;
      });
      this.setState({ RefreshTreeData: node.itemsSource });

      const empty = [];
      this.setState({ wdata_category: empty });
      this.setState({
        wdata_category: this.state.wdata_category.concat(tree_data),
      });

      if (tree_data.length !== 0) {
        if (tree_data[0].type === "category") {
          // console.log("lent is", tree_data.length);
          this.setState({ wdata_category_length: tree_data.length });
        }
        if (tree_data[0].type === "product") {
          if (this.state.wdata_category[0].type === "category") {
            console.log("i am sec if");
            const wdata_category = [...this.state.wdata_category];
            wdata_category.splice(0, this.state.wdata_category_length);

            this.setState({
              wdata_category,
            });
          }
        }
      }

      this.setState({ wdata_length: this.state.wdata_category.length });

      this.setState({ page: 1 });
      this.page = 1;
      this.setState({ check_bool2: false });
      console.log("lenth is", this.state.wdata_category);

      return this.state.wdata_category;
      //callback(this.state.wdata_category);
    });
    return rtt;
    //console.log("rt", rtt);
  };

  fetchWarehouse = async () => {
    localStorage.setItem("check_render", true);
    let node = this._control2.selectedNode;
    let sp = this.myRef.current;

    var control = this._control2;
    var wdata_key = localStorage.getItem("wdata_key");
    var wdata_name = localStorage.getItem("wdata_name");
    var node_level = localStorage.getItem("node_level");

    var base_url = this.server_url + "/api/trees/warehouses";
    var url = "";
    console.log("to come in if", node.dataItem.key, wdata_key);
    if (node.dataItem.key !== wdata_key) {
      await this.setState({ check_bool: true });
      if (node.hasChildren === true && node.hasPendingChildren === true) {
        console.log("i am setting cond", node);
        node_level = "0";
      }
      var tree = node.treeView;
      console.log("true and remove items is", node.dataItem, this.state.page);
      // remove previous lazy-loaded items from data source
      node.dataItem.items.length = 0;
      //\

      await console.log("after select node_level", node);
      // re-bind the tree to remove the old nodes

      await tree.loadTree();

      console.log("selected node", node);
      wdata_key = node.dataItem.key;
      console.log(
        "selected node is",
        node.dataItem.key,
        "from local",
        wdata_key
      );
    }

    console.log("before increment", this.state.page);
    this.page++;
    //await this.setState({ page: this.page });

    //let { page } = this.state;

    console.log("node level is", node_level, this.page);
    if (node_level === "0") {
      url =
        base_url +
        "?page=" +
        this.page +
        "&limit=1&warehouse=" +
        String(wdata_key);
    } else {
      url =
        base_url +
        "?page=" +
        this.page +
        "&limit=5&parent=" +
        String(wdata_key) +
        "&warehouse=" +
        String(wdata_name);
    }

    axios.get(url).then(async (res) => {
      let data = res.data.data.results;
      console.log("axios is", data, url);
      let tree_data11 = data.map((elem) => {
        let json = {};
        if (elem.type === "category") {
          json.warehouse = elem.warehouse;
        }
        if (elem.type === "category" || elem.type === "warehouse") {
          //console.log("i am first");
          json.header = elem.name;
          json.key = elem._id;
          json.type = elem.type;
          json.items = [];
        } else if (elem.type === "productCategory") {
          // console.log("i am second");
          json.header = elem.warehouse_product.title;
          json.key = elem.warehouse_product._id;
          json.type = elem.warehouse_product.type;
        }
        return json;
      });

      let node = this._control2.selectedNode;
      node.isCollapsed = false;
      let find_ware = node.dataItem.items;
      console.log("node is", find_ware, "tree data", tree_data11);

      if (find_ware !== null) {
        Array.prototype.push.apply(find_ware, tree_data11);
      }
      if (tree_data11.length === 0) {
        //localStorage.setItem("button_check", false);
        await this.setState({ check_bool2: true });
        console.log("i am reached", this.state.check_bool2);
      }

      node.refresh();
    });
  };
}

export default Mappings;
